import { Moon, Palette } from 'lucide-react';
import { Theme } from '@/app/page';

interface ThemeToggleProps {
  currentTheme: Theme;
  onToggle: () => void;
}

export default function ThemeToggle({ currentTheme, onToggle }: ThemeToggleProps) {
  return (
    <button
      onClick={onToggle}
      className={`flex items-center space-x-3 px-6 py-3 rounded-xl transition-all duration-500 transform hover:scale-105 ${
        currentTheme === 'dark'
          ? 'bg-gradient-to-r from-gray-800 to-gray-900 text-gray-100 shadow-xl border border-gray-700'
          : 'bg-gradient-to-r from-indigo-500 to-purple-600 text-white shadow-xl'
      }`}
    >
      <div className="relative w-8 h-8">
        <div className={`absolute inset-0 flex items-center justify-center transition-all duration-500 ${
          currentTheme === 'dark' ? 'opacity-100 rotate-0' : 'opacity-0 rotate-90'
        }`}>
          <Moon className="w-6 h-6" />
        </div>
        <div className={`absolute inset-0 flex items-center justify-center transition-all duration-500 ${
          currentTheme === 'gradient' ? 'opacity-100 rotate-0' : 'opacity-0 -rotate-90'
        }`}>
          <Palette className="w-6 h-6" />
        </div>
      </div>
      <div className="text-left">
        <div className="font-semibold text-sm">
          Switch to {currentTheme === 'dark' ? 'Gradient' : 'Dark'} Mode
        </div>
        <div className="text-xs opacity-80">
          {currentTheme === 'dark' ? 'Vibrant colors' : 'Dark elegance'}
        </div>
      </div>
      
      {/* Toggle indicator */}
      <div className={`w-12 h-6 rounded-full p-1 transition-all duration-300 ${
        currentTheme === 'dark' ? 'bg-gray-700' : 'bg-indigo-400'
      }`}>
        <div className={`w-4 h-4 rounded-full bg-white transition-all duration-300 transform ${
          currentTheme === 'dark' ? 'translate-x-0' : 'translate-x-6'
        }`} />
      </div>
    </button>
  );
}